import { useState, useRef, useEffect } from 'react';
import { useAuth } from '../context/AuthContext';
import Message from './Message';
import ChatInput from './ChatInput';

// Set your backend API URL in .env.local
const API_BASE = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api';

// Example prompts from your screenshot
const examplePrompts = [
  'How to make a new Passport?',
  'What documents do I need for a CNIC?',
  'Summarize the new property tax law.',
];

export default function ChatWindow() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const { token } = useAuth(); // Get auth token from context
  const messagesEndRef = useRef(null);

  // Scroll to bottom whenever messages change
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!input.trim() || isLoading) return;

    const userMessage = { sender: 'user', text: input };
    setMessages((prev) => [...prev, userMessage]);
    setInput('');
    setIsLoading(true);

    try {
      // This fetch is based on your index.html logic
      const res = await fetch(`${API_BASE}/ai/chat`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          // Send token if it exists (for logged-in users)
          Authorization: token ? `Bearer ${token}` : '',
        },
        body: JSON.stringify({ message: input }),
      });

      const data = await res.json();

      if (res.ok && data.reply) {
        setMessages((prev) => [
          ...prev,
          { sender: 'ai', text: data.reply },
        ]);
      } else {
        throw new Error(data.message || 'Sorry, something went wrong.');
      }
    } catch (error) {
      setMessages((prev) => [
        ...prev,
        { sender: 'ai', text: error.message },
      ]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleExampleClick = (prompt) => {
    setInput(prompt);
  };

  return (
    <div className="flex-1 flex flex-col h-full overflow-hidden">
      {/* Message List */}
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        <div className="container mx-auto max-w-3xl">
          {messages.length === 0 ? (
            // Welcome Screen (from your screenshot)
            <div className="text-center mt-20">
              <h1 className="text-4xl font-bold text-white mb-4">Awaam AI</h1>
              <p className="text-lg text-gray-400 mb-12">
                Your AI assistant for Pakistani public documents.
              </p>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                {examplePrompts.map((prompt) => (
                  <button
                    key={prompt}
                    onClick={() => handleExampleClick(prompt)}
                    className="bg-gray-800 border border-gray-700 p-4 rounded-lg text-left hover:bg-gray-700 transition-colors"
                  >
                    <p className="font-semibold text-white">{prompt}</p>
                  </button>
                ))}
              </div>
            </div>
          ) : (
            // Chat messages
            messages.map((msg, index) => (
              <Message key={index} sender={msg.sender} text={msg.text} />
            ))
          )}

          {/* Typing indicator */}
          {isLoading && <Message sender="ai" text="â€¦" />}

          {/* Empty div to scroll to */}
          <div ref={messagesEndRef} />
        </div>
      </div>

      {/* Input Form */}
      <ChatInput
        input={input}
        setInput={setInput}
        onSubmit={handleSubmit}
        isLoading={isLoading}
      />
    </div>
  );
}